import "../styles/projectExplorer.css";
import "../styles/keyframes.css";

import projectsJSON from "../json/projects.json";
import { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { XCircleIcon as XIconOutline } from "@heroicons/react/24/outline";

export default function ProjectExplorer() {
  // JSON data of the projects
  const [projectsDict, setProjectsDict] = useState({});
  // Current open projects in the explorer
  const [openProjects, setOpenProjects] = useState([]);
  // The current selected project
  const [selectedProject, setSelectedProject] = useState("");
  // Navigation tool
  const navigate = useNavigate();
  // URL parameters tool
  const [searchParams, setSearchParams] = useSearchParams();
  const [pageLoaded, setPageLoaded] = useState(false);
  const [hasPageRefreshed, setHasPageRefreshed] = useState(false);

  // Load the projects data from the JSON file
  useEffect(() => {
    // Set the open projects to the base
    setOpenProjects((prev) => {
      return [...prev, projectsJSON["base"]];
    });
    // Set the selected project to the base
    setSelectedProject(projectsJSON["base"]["title"]);
    // Set the project dict
    setProjectsDict(projectsJSON);
  }, []);

  // Fetch the open project based on the URL parameter
  // Note: This runs whenever a URL change is detected
  useEffect(() => {
    if (Object.keys(projectsDict).length > 0) {
      // Fetch the project name from the URL parameter
      const project = searchParams.get("project");

      // If no URL parameter is found
      if (!project) {
        setSelectedProject(projectsDict["base"]["title"]);
        setPageLoaded(true);
        return;
      }

      // Determine if the selected project is already open in the project explorer
      let projectAlreadyOpen = false;
      for (let index = 0; index < openProjects.length; index++) {
        if (
          openProjects[index]["title"].toLowerCase() === project.toLowerCase()
        ) {
          projectAlreadyOpen = true;
        }
      }

      // If the project is already open in the project explorer
      if (projectAlreadyOpen) {
        // Set the selected project based on URL parameter
        setSelectedProject(project.toLowerCase());
        setPageLoaded(true);
        return;
      }

      // Determine if the selected project actually exists
      let projectExists = false;
      let selectedProjectIndex;
      for (let index = 0; index < projectsDict["projects"].length; index++) {
        // If the project is found in the dict
        if (
          projectsDict["projects"][index]["title"].toLowerCase() ===
          project.toLowerCase()
        ) {
          projectExists = true;
          selectedProjectIndex = index;
        }
      }

      // If the project doesn't exist (URL paramteter is invalid)
      if (!projectExists) {
        window.location.href = "#" + projectsDict["base"]["redirect"];
        return;
      }

      // Add the project title + redirect link to the openProjects
      setOpenProjects((prev) => {
        return [
          ...prev,
          {
            title: projectsDict["projects"][selectedProjectIndex]["title"],
            redirect:
              projectsDict["redirect"] +
              projectsDict["projects"][selectedProjectIndex][
                "title"
              ].toLowerCase(),
          },
        ];
      });
      setSelectedProject(
        projectsDict["projects"][selectedProjectIndex]["title"]
      );
      setPageLoaded(true);
    }
  }, [window.location.href, projectsDict]);

  // If the URL changes away from the base page, set a flag that indicates
  // to not set the fade-in animation
  // Note: Hacky solution
  useEffect(() => {
    if (selectedProject && projectsDict) {
      if (
        selectedProject.toLowerCase() !=
        projectsDict["base"]["title"].toLowerCase()
      ) {
        setHasPageRefreshed(true);
      }
    }
  }, [window.location.href]);

  return (
    <div className="project-explorer">
      {pageLoaded &&
        openProjects.map((project, index) => {
          return (
            <div
              onClick={() => {
                console.log("nav here");
                navigate(project["redirect"]);
              }}
              className={
                // If the project is selected
                project["title"].toLowerCase() === selectedProject.toLowerCase()
                  ? // if the project is the base and we are on the base and haven't refreshed
                    index === 0 && !hasPageRefreshed
                    ? "project-explorer-nav project-explorer-selected project-explorer-nav-base"
                    : // If the project is selected but is not the base
                      "project-explorer-nav project-explorer-selected"
                  : // If the project isn't selected
                    "project-explorer-nav"
              }
            >
              <p>{project["title"]}</p>
              {project["title"].toLowerCase() !=
                projectsDict["base"]["title"].toLowerCase() && (
                <div
                  // Close the project
                  onClick={(event) => {
                    event.stopPropagation();
                    let newProjectIndex = 0;
                    for (let index = 0; index < openProjects.length; index++) {
                      if (
                        project["title"].toLowerCase() ===
                        openProjects[index]["title"].toLowerCase()
                      ) {
                        newProjectIndex = index - 1;
                      }
                    }
                    setOpenProjects(
                      openProjects.filter(
                        (openProject) =>
                          openProject["title"].toLowerCase() !=
                          project["title"].toLowerCase()
                      )
                    );

                    // Only navigate away if we have the current project open
                    if (
                      selectedProject.toLowerCase() ===
                      project["title"].toLowerCase()
                    ) {
                      navigate(openProjects[newProjectIndex]["redirect"]);
                    }
                  }}
                >
                  <XIconOutline className="project-explorer-nav-x-outline"></XIconOutline>
                </div>
              )}
            </div>
          );
        })}
    </div>
  );
}
