import "../styles/portfolio.css";
import "../styles/keyframes.css";
import imagesJSON from "../json/projects.json";
import ProjectPDF from "../components/projectPDF";
import { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";

export default function Portfolio() {
  // JSON data of the projects
  const [projectsDict, setProjectsDict] = useState({});
  // Whether the project data has been fetched or not
  const [projectsLoaded, setProjectsLoaded] = useState(false);
  // The current open project
  const [openProject, setOpenProject] = useState({
    title: "Projects",
    pdfPath: "",
    redirect: "/projects/",
  });
  // Navigation tool
  const navigate = useNavigate();
  // URL parameters tool
  const [searchParams] = useSearchParams();

  // Determine whether the image paths have been loaded into the image dict
  useEffect(() => {
    if (Object.keys(projectsDict).length > 0) {
      setProjectsLoaded(true);
    }
  }, [projectsDict]);

  // Load an image map from the JSON file
  useEffect(() => {
    setProjectsDict(imagesJSON);
  }, []);

  // When we detect a url change or when we first load the page
  useEffect(() => {
    // If all project data has been loaded
    if (projectsLoaded) {
      const project = searchParams.get("project");
      // If no url parameter is given, redirect to the base
      if (!project) {
        setOpenProject({
          title: projectsDict["base"]["title"],
          redirect: projectsDict["base"]["redirect"],
        });
        return;
      }
      // Loop through each project
      for (let index = 0; index < projectsDict["projects"].length; index++) {
        // If the url parameter is a valid project title
        if (
          projectsDict["projects"][index]["title"].toLowerCase() === project
        ) {
          // Set the open project
          setOpenProject({
            title: projectsDict["projects"][index]["title"],
            pdfPath: projectsDict["projects"][index]["pdfPath"],
            redirect:
              projectsDict["redirect"] +
              projectsDict["projects"][index]["title"].toLowerCase(),
          });
        }
      }
    }
  }, [window.location.href, projectsLoaded]);

  return (
    <div className="portfolio-container">
      {openProject && openProject["title"] === "Projects" && (
        <>
          <div className="portfolio-title">
            <div className="wave-text">
              <p>Projects</p>
              <p>Projects</p>
            </div>
            <p>Explore My Previous Projects</p>
          </div>
          <div className="portfolio-grid">
            {projectsLoaded &&
              projectsDict["projects"].map((card) => {
                return (
                  <div
                    className="card-container"
                    key={card.id}
                    onClick={() => {
                      navigate(
                        projectsDict["redirect"] + card["title"].toLowerCase()
                      );
                    }}
                  >
                    <div className="card-image">
                      <img src={card["image"]} alt="img"></img>
                    </div>
                    <div className="card-title">
                      <p>{card.title}</p>
                    </div>
                    <div className="card-description">
                      <p>{card.description}</p>
                    </div>
                    <div className="link-container">
                      <a href={card.link} target="_blank" rel="noreferrer">
                        <img src={projectsDict["link-icon"]} alt="img"></img>
                      </a>
                    </div>
                  </div>
                );
              })}
          </div>
        </>
      )}
      {openProject && openProject["pdfPath"] && (
        <ProjectPDF path={openProject["pdfPath"]}></ProjectPDF>
      )}
    </div>
  );
}
